import { Component, OnInit } from '@angular/core';
import { Apollo } from 'apollo-angular';
import { RESET_PASSWORD } from '../mutations/resetPassword';
import { REQUEST_RESET } from '../mutations/requestReset';
import { UPDATE_USER } from '../mutations/updateUser';
import { MatDialog } from '@angular/material/dialog';
import { DialogComponent } from '../dialog/dialog.component';
import { RequestResetResponse } from '../interfaces';
import { User } from '../types';
import pick from 'lodash.pick';
import { Location } from '@angular/common';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.scss'],
})
export class ForgotPasswordComponent implements OnInit {
  isRecovering: Boolean = false;
  isCodeValid: Boolean = false;
  user: User;
  constructor(
    private apollo: Apollo,
    private dialog: MatDialog,
    private location: Location
  ) {}

  reset(email: string): void {
    this.apollo
      .mutate<RequestResetResponse>({
        mutation: REQUEST_RESET,
        variables: { email },
      })
      .subscribe({
        next(data) {
          this.user = data.data.user;
        },
        error(err) {
          this.dialog.open(DialogComponent, {
            data: 'There was an error requesting the reset code',
          });
        },
        complete() {
          this.isRecovering = true;
        },
      });
  }

  verifyCode(code: string): void {
    this.apollo
      .mutate({ mutation: RESET_PASSWORD, variables: { token: code } })
      .subscribe({
        next(data) {
          console.log(data);
        },
        error(err) {
          this.dialog.open(DialogComponent, {
            data: 'There was an error with the reset code',
          });
        },
        complete() {
          this.isCodeValid = true;
          this.isRecovering = false;
        },
      });
  }

  changePassword(password: string, confirm: string) {
    if (password === confirm) {
      const data = pick(this.user, [
        'name',
        'username',
        'email',
        'address',
        'country',
        'type',
        'fileUrl',
      ]);
      const userData = { ...data, password };
      this.apollo
        .mutate({
          mutation: UPDATE_USER,
          variables: { where: this.user._id, userData },
        })
        .subscribe({
          next(data) {
            console.log(data);
          },
          error(err) {
            this.dialog.open(DialogComponent, {
              data: 'There was an error updating the password',
            });
          },
          complete() {
            this.location.go('/login');
            this.dialog.open(DialogComponent, {
              data: 'Password updated successfully',
            });
          },
        });
    } else {
      this.dialog.open(DialogComponent, {
        data: 'Confirm password must match!',
      });
    }
  }

  ngOnInit(): void {}
}
